import Header from './Header'
import {useState}from 'react'
import { Table } from 'react-bootstrap'

function SearchProduct(){

const[data,setData]=useState([])

async function search(key){

console.warn(key)

let result = await fetch("https://abdulazizecom.herokuapp.com/api/search"+key);
result = await result.json();
setData(result)
}


    return(

<div>
<Header>

</Header>


<div className="col-sm-6 offset-sm-3">

<h1>Search Product</h1>
    <br />
    <input type="text" className="form-control" placeholder="Search" onChange={(e)=> search(e.target.value)} />
    <Table>

<tr>

    <td>Id</td>
    <td>Name</td>
    <td>Price</td>
    <td>Description</td>
    <td>Image</td>
    <td>Operations</td>
</tr>
{

    data.map((item) =>
        <tr>
            <td>{item.id}</td>
            <td>{item.name}</td>
            <td>{item.price}</td>
            <td>{item.descrption}</td>
            <td>{item.file_path}</td>
            <td><img style={{ width: 100 }} src={"localhost:8000/" + item.file_path} /></td>
   
        </tr>
    )
}
</Table>
</div>
</div>




    )
}
export default SearchProduct